body{font-family:'Open Sans',sans-serif;font-size:18px;color:#fff;font-weight:400;font-style:normal;padding-bottom:20vh;background-color:#010000}.container-fluid{line-height:24px}footer{width:100%;background:#191614;color:#fff;height:7.5vh;position:fixed;bottom:0;left:0;margin:0}footer nav{padding:1.5vh 0}footer .navbar{padding:0;margin:0}footer .item{text-align:center;justify-content:center}footer nav ul{display:flex}footer nav ul li{list-style-type:none;flex:1}footer nav ul li a{justify-content:center}footer nav ul li svg{height:4.5vh;width:auto}footer nav ul li svg ::selection{background-color:transparent}footer a{text-decoration:none;stroke:#fff;opacity:.7;-webkit-transition:.4s ease-in-out;-moz-transition:.4s ease-in-out;transition:.4s ease-in-out;cursor:pointer}footer a:hover{opacity:1;-webkit-transition:.4s ease-in-out;-moz-transition:.4s ease-in-out;transition:.4s ease-in-out;text-decoration:none}footer a:active{color:#ffdc81;opacity:1;-webkit-transition:.4s ease-in-out;-moz-transition:.4s ease-in-out;transition:.4s ease-in-out;text-decoration:none}footer .selected{opacity:1;stroke:#ffdc81;-webkit-transition:.4s ease-in-out;-moz-transition:.4s ease-in-out;transition:.4s ease-in-out;text-decoration:none}footer .selected svg{stroke:#ffdc81;-webkit-transition:.4s ease-in-out;-moz-transition:.4s ease-in-out;transition:.4s ease-in-out;text-decoration:none}